#profile {
  font-size: 1.5rem;
  background-color: #7dedff;
}

.dropdown {
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 1rem;
  color: white;
  padding: 0.25rem 0.5rem;
}

.dropdown:hover .dropbtn {
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #0fa,
    0 0 82px #aae8f1, 0 0 92px #aae8f1, 0 0 102px #aae8f1, 0 0 151px #0fa;
}

.dropdown-menu {
  display: none;
  position: absolute;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  right: inherit;
}

.dropdown-item {
  float: none;
  padding: 0.25rem 0.5rem;
  text-decoration: none;
  display: block;
  background-color: white;
  text-align: left;
}

.sign-out-btn {
  color: red;
}

.sign-out-btn:hover {
  color: white;
  background-color: red;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.title {
  margin-bottom: 1rem;
  color: #7dedff;
  text-align: center;
  font-size: 1.75rem;
  font-weight: normal;
}

.container {
  position: absolute;
  width: 100%;
  top: 10vh;
  left: -100%;
  transition: left 500ms ease-in;
}

.lists .item {
  border: solid;
  border-image: linear-gradient(to left, #fff 1%, #7dedff 50%) 100% 0 100% 0/3px
    0 3px 0;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.del-list-btn {
  padding: 0.25rem;
}

.del-list-btn:hover {
  border-radius: 5px;
  fill: white;
  background-color: red;
}

.lists .item a {
  flex: 1 0 90%;
  padding: 1rem;
  display: block;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todos ul {
  display: none;
}

.todo {
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.todo p {
  flex-basis: 85%;
}

.todo-btns {
  display: flex;
  justify-content: center;
}

.todo-btns > * {
  transition: fill 200ms ease-in-out;
}

.todo p.completed {
  text-decoration: line-through;
  color: #bebebe;
  font-style: italic;
}

.todo p.completed + .todo-btns .tick-btn {
  fill: #0fa;
}

.activeContainer {
  left: 0;
  transition: left 500ms ease-out;
}

.todos ul.activeTodos {
  display: block;
}
